<template>
  <div class="login" id="login">
    <div class="login-log">
      <img alt="" src="./assets/img/logo1.png"/>
      <div class="log_text">智能风控平台</div>
    </div>
    <div class="login-form">
      <div class="login-form-title">
        <span>用户登录</span>
      </div>
      <ui-form ref="login" :model="login" :rules="validate">
        <ui-row>
          <ui-col span="5">
            <ui-form-item prop="username">
              <ui-input type="text" v-model="login.username" ref="input1" placeholder="请输入用户名"
                        size="large" :autofocus="true" @keyup.native="formSubmit($event)">
                <ui-icon type="person" slot="prepend"></ui-icon>
              </ui-input>
            </ui-form-item>
          </ui-col>
        </ui-row>
        <ui-row>
          <ui-col span="5">
            <ui-form-item prop="password">
              <ui-input type="password" v-model="login.password" placeholder="请输入用户密码" size="large"
                        @keyup.native="formSubmit($event)">
                <ui-icon type="locked" slot="prepend"></ui-icon>
              </ui-input>
            </ui-form-item>
          </ui-col>
        </ui-row>
        <ui-row>
          <ui-col span="5">
            <ui-form-item prop="">
              <ui-button long class="login-btn" @click="handleSubmit('login')">登录</ui-button>
            </ui-form-item>
          </ui-col>
        </ui-row>
      </ui-form>
    </div>
    <div class="login-footer">
      <p>COPYRIGHT © 2017 深圳金证科技股份有限公司</p>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import {mapGetters} from 'vuex';
  import {enc} from '@/assets/js/utils';
  import Cookies from 'js-cookie';

  export default{
    data () {
      return {
        login: {
          username: '',
          password: ''
        },
        validate: {
          username: [{required: true, message: '请输入登录名', trigger: 'blur'}],
          password: [{required: true, message: '请输入登录密码', trigger: 'blur'}]
        },
        help: false
      };
    },
    computed: {
      ...mapGetters(['last_log_user', 'last_log_time'])
    },
    methods: {
      handleSubmit (name) {
        // 校验成功后跳转到主框架页面
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.$store.dispatch('login', {
              data: {
                username: this.login.username,
                password: enc(this.login.password)
              },
              success: this.success
            });
          }
        });
      },
      formSubmit (event) {
        if (event.keyCode === 13) {
          this.handleSubmit('login');
        }
      },
      success () {
        let response = this.$store.getters.jsonData;
        if (response.flag === '0') {
          this.$store.commit('CLEAR_OPEND_TAG', this.$router);
          let userData = (response.results[0].record.length > 0) ? response.results[0].record[0] : null;
          if (userData) {
            Cookies.set('user', userData.loginID);
            this.$store.dispatch('loginStorage', userData);
            this.getPageSuccess();
          }
        } else {
          this.$uiMessage.error({
            content: response.prompt,
            duration: 3
          });
        }
      },
      getPageSuccess () {
        this.$router.replace('/frame');
      }
    }
  };
</script>



